import React from "react";
import images from "../../utils/constants";
import { Swiper, SwiperSlide } from "swiper/react";
import { Mousewheel } from "swiper";
import "swiper/css";
import "./Roles.css";

function Roles() {
  return (
    <section className="roles">
      <h2 className="roles__title">   &nbsp;  </h2>
      <Swiper
        direction={"horizontal"}
        slidesPerView={2}
        spaceBetween={25}
        mousewheel={true}
        modules={[Mousewheel]}
        className="roles__slider"
      >
        <ul className="roles__container">
          <SwiperSlide>
            <li className="role">
              <img className="role__img" src={images.cloud} alt="" />
              <div className="role__wrap">
                <h3 className="role__title"> </h3>
                <p className="role__subtitle">
                   ,    ,    
                   
                </p>
              </div>
            </li>
          </SwiperSlide>
          <SwiperSlide>
            <li className="role">
              <img className="role__img" src={images.cloud} alt="" />
              <div className="role__wrap">
                <h3 className="role__title">   </h3>
                <p className="role__subtitle">
                           - 
                    
                </p>
              </div>
            </li>
          </SwiperSlide>
          <SwiperSlide>
            <li className="role">
              <img className="role__img" src={images.cloud} alt="" />
              <div className="role__wrap">
                <h3 className="role__title">  </h3>
                <p className="role__subtitle">
                          
                   
                </p>
              </div>
            </li>
          </SwiperSlide>
          <SwiperSlide>
            <li className="role">
              <img className="role__img" src={images.cloud} alt="" />
              <div className="role__wrap">
                <h3 className="role__title"> </h3>
                <p className="role__subtitle">
                   35  55  . , 2-3   , 
                   
                </p>
              </div>
            </li>
          </SwiperSlide>
        </ul>
      </Swiper>
    </section>
  );
}

export default Roles;
